@import '../../assets/assets.scss';
*{
    margin: 0;
    padding: 0;
}
.nav{
    height: 100%;
    width: 50px;
    background-color: $primary-color;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    position: fixed;
    z-index: 999999;
    top: 0;
    right: 0;
    &__software, &__ux, &__about{
        font-family: $font-primary;
        color: $tertiary-color;
        transform: rotate(90deg);
        font-weight: 500;
        margin: 5px;
        width: max-content;
        display: flex;
        justify-content: center;
        padding-top: 13px;
        cursor: pointer;
    }
}

@media only screen and (max-width: 450px){
    .nav{
        width: 30px;
        &__software, &__ux, &__about{
            padding-top: 10px;
            font-size: 13px;
        }
    }
}

.softwareOff{
    animation-name: softwarePopoutOFF;
    animation-duration: 1.3s;
    height: 100vh;
    position: fixed;
    top: 0;
    right: -66vw;
    z-index: 50;
}
@keyframes softwarePopoutOFF {
    0%{
        position: fixed;
        top: 0;
        right: 0;
    }
    100%{
        right: -66vw;
    }
}
.softwareOn{
    animation-name: softwarePopoutON;
    animation-duration: 1.3s;
    height: 100vh;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 50;
    margin-right: 0;
    overflow-y: hidden;
}
@keyframes softwarePopoutON {
    0%{
        right: -66vw;
    }
    100%{
        height: 150vh;
        position: fixed;
        top: 0;
        right: 0;
    }
}

